      // #3
      let $container = $("#testTable").clone()    // 프린트 할 특정 영역 복사
      let cssText = ""                            // 스타일 복사
      for (const node of $("style")) {
          cssText += node.innerHTML
      }
      /** 팝업 */
      let testIMG = "/img/logo.5d882bc8.png"
      let innerHtml = $container[0].innerHTML
      let popupWindow = window.open("", "_blank", "width=700; height=800")
      console.log("1",innerHtml);
      popupWindow.document.write(
        // "<!DOCTYPE html>"+
        // "<html>"+
        //   "<head>"+
        //   "<style>"+".width100{ width:100%; margin:0 auto; }"+cssText+"</style>"+
        //   "</head>"+
        //   "<body>"+
        //   "<h1>TEST</h1>"+"<img src="+testIMG+"/>"
        //   +innerHtml+"</body>"+
        // "</html>"
        `<!DOCTYPE html>
        <html>
          <head>
          <style>.width100{ width:100%; }${cssText}</style>
