        popupWindow.print()         // 팝업의 프린트 도구 시작
        popupWindow.close()         // 프린트 도구 닫혔을 경우 팝업 닫기
      }, 1000)
    },
    excel(){
      // var a = document.createElement('a');
      // var data_type = 'data:application/vnd.ms-excel';
      // var table_html = encodeURIComponent($("#testTable").html());
      // a.href = data_type + ', ' + table_html;
      // a.download = '파일명.xls';
      
      // a.click();


      // // #1
      // var a = document.createElement('a');
      // let $container = $("#testTable").clone()    // 프린트 할 특정 영역 복사
      // let cssText = ""                            // 스타일 복사
      // for (const node of $("style")) {
      //     cssText += node.innerHTML
      // }
      // /** 팝업 */
      // let innerHtml = $container[0].innerHTML
      // let popupWindow = "<!DOCTYPE html>"+
      //   "<html>"+
      //     "<body>"+innerHtml+"</body>"+
      //   "</html>"
      
      // var data_type = 'data:application/vnd.ms-excel';
      // var table_html = encodeURIComponent(popupWindow);

      // a.href = data_type + ', ' + table_html;
      // a.download = '파일명.xls';
      
      // a.click();

      // #2
      var a = document.createElement('a');
      var table = document.getElementById("testTable");
      var html = table.outerHTML;
      var url = 'data:application/vnd.ms-excel,' + escape(html); // Set your html table into url 
      a.setAttribute("href", url);
      a.setAttribute("download", "export.xls"); // Choose the file name
      a.click();
    },
    async makePDF() {
      // window.html2canvas = html2canvas //Vue.js 특성상 window 객체에 직접 할당해야한다.
			let that = this
			let pdf = new jsPDF('p', 'mm', 'a4')
			let canvas = pdf.canvas
			const pageWidth = 210 //캔버스 너비 mm
			const pageHeight = 295 //캔버스 높이 mm
			canvas.width = pageWidth

      let ele = document.querySelector('#testTable')
			let width = ele.offsetWidth // 셀렉트한 요소의 px 너비
			let height = ele.offsetHeight // 셀렉트한 요소의 px 높이
			let imgHeight = pageWidth * height/width // 이미지 높이값 px to mm 변환

			if(!ele){
				console.warn(selector + ' is not exist.')
				return false
      }

      // html을 데이터 이미지화 처리
      var options = { type: 'dataURL'}
      this.output = await this.$html2canvas(ele,options)
      
      let position = 0
      const imgData = this.output
      pdf.addImage(imgData, 'png', 0, position, pageWidth, imgHeight, undefined, 'slow')

      //Paging 처리
      let heightLeft = imgHeight //페이징 처리를 위해 남은 페이지 높이 세팅.
      heightLeft -= pageHeight
      while (heightLeft >= 0) {
        position = heightLeft - imgHeight
        pdf.addPage();
        pdf.addImage(imgData, 'png', 0, position, pageWidth, imgHeight)
        heightLeft -= pageHeight
      }
      // pdf.autoPrint(); // 파일을 열때 프린트 여부를 물음
      pdf.save( 'pdf_test.pdf');
      // console.log("this.output",this.output)

    }
